:root {
  --black: #111;
  --theme: #678;
  --background: #F1F3F5;
  --blue: #00BFED;
  --light-green: #00D7C5;
  --purple: #AD1DE6;
  --bg-black: #303E4B;
  --yellow: #E99523;
  --red: #D81C60;
  --font-size: 16px;
}

* {
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  -moz-user-select: -moz-none;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  color: #111;
  color: var(--black);
}

body::-webkit-scrollbar {
  width: 10px;
}

body::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
}

body::-webkit-scrollbar-thumb {
  background-color: #678;
  background-color: var(--theme);
  outline: 1px solid #111;
  outline: 1px solid var(--black);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


header {
    width: 100%;
    height: 12vh;
    position: fixed;
    z-index: 10;
    top: 0;
    background: #fff;
}

#header-container {
    width: 87%;
    height: 100%;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

#header-right ul {
    list-style: none;
}


#header-right li {
    display: inline-block;
    margin-left: 3.5vw;
}

#header-right a {
    text-decoration: none;
    color: var(--black);
}

main {
    min-height: 65vh;
    width: 100%;
    margin-top: 12vh;
}

footer {
    padding: 25px 0;
    width: 100%;
    min-height: 15vh;
    bottom: 0;
    background: #eee;
}

#footer-container {
    width: 75%;
    display: flex;
    flex-wrap: wrap;
    margin: 0 auto;
    justify-content: space-between;
    align-items: center;
}

#footer-container > * {
    flex: 1 1;
}

.footer-link {
    text-align: right;
    margin: 10px 0;
    font-size: 1.7vh;
}

.footer-link > *{
    display: inline-block;
}

#claim {
    text-align: left;
    opacity: 0.7;
}

#claim small {
    font-size: 10px;
}

@media only screen and (min-width: 2000px) {


    #header-right li {
        font-size: 1.2vw;
    }

    #claim small {
        font-size: 0.8vw;
    }

}

@media only screen and (max-width: 768px) {

    header {
        height: 15vh;
    }

    #header-container {
        width: 90%;
    }

    .footer-link {
        font-size: 2vh;
    }

}

@media only screen and (max-width: 480px) {

    #header-container {
        width: 80%;
    }

    nav {
        display: none;
    }

}

.link-button, .link-button:visited {
    cursor: pointer;
    position: relative;
    padding: 15px 40px;
    border-radius: 10vw;
    box-shadow: 8px 10px 20px 0 rgba(46,61,73,.15);
    -webkit-transition: all 0.4s;
    transition: all 0.4s;
    text-decoration: none;
    text-align: center;
    color: white;
    background: var(--light-green);
    font-weight: bold;
}

.link-button:hover, .link-button:focus {
    box-shadow: 2px 4px 8px 0 rgba(46,61,73,.25);
}

@media only screen and (min-width: 2000px) {
    .link-button {
        padding: 1vw 3vw;
        border-radius: 2vw;
    }
}

@media only screen and (max-width: 768px) {
    .link-button {
        padding: 8px 25px;
    }
}
@media only screen and (min-width: 1024px) {
    .logo {
        font-size: 2vw;
    }
}

@media only screen and (max-width: 480px) {
    .logo {
        font-size: 3.5vh;
    }
}
#hamburger-menu {
    position: relative;
    min-width: 27px;
    min-height: 15px;
}

.bar {
    width: 100%;
    height: 3px;
    background-color: var(--black);
    position: absolute;
}

.bar:first-child {
    top: 0;
}

.bar:nth-child(1) {
    top: 50%;
    margin-top: -1.5px;
}

.bar:last-child {
    bottom: 0;
}

@media only screen and (min-width: 768px) {
    #hamburger-menu {
        display: none;
    }
}
.backdrop-container {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 12;
    background: rgba(0, 0, 0, 0.25);
}
.sliding-nav-container {
    height: 100%;
    width: 70%;
    position: absolute;
    z-index: 13;
    background-color: var(--bg-black);
    box-shadow: 8px 10px 20px 0 rgba(46,61,73,.15);
    top: 0;
    left: 0;
    -webkit-transition: -webkit-transform 0.2s;
    transition: -webkit-transform 0.2s;
    transition: transform 0.2s;
    transition: transform 0.2s, -webkit-transform 0.2s;
}

#sliding-nav-header {
    width: 80%;
    margin: 5vh auto;
    display: flex;
    justify-content: space-between;
}

#back-button {
    width: 0; 
    height: 0; 
    border-top: 12px solid transparent;
    border-bottom: 12px solid transparent; 
    border-right:12px solid #fff;
}

@media only screen and (min-width: 1024px) {
    .sliding-nav-container {
        display: none;
    }
}
.sliding-nav-link-container {
    line-height: 7vh;
    padding-left: 8vw;
    color: #fff;
    border-bottom: 0.5px solid #678;
}
#home-page-container {
    padding: 7vh 0;
    text-align: center;
}

.heading {
    margin-bottom: 5px;
    color: var(--black);
    font-size: 2.5vw;
}

.sub-heading {
    color: var(--theme);
    font-size: 1.5vw;
    max-width: 75vw;
    margin: 17px auto;
}

@media only screen and (min-width: 2000px) {

    #home-page-container {
        padding: 10vh 0; 
    }

    .heading {
        margin-bottom: 5px;
        color: var(--black);
        font-size: 3vw;
    }

    .sub-heading {
        font-size: 1.5vw;
    }
}

@media only screen and (max-width: 768px) {

    #home-page-container {
        padding: 10vh 0; 
    }

    .heading {
        margin-bottom: 5px;
        color: var(--black);
        font-size: 4vw;
    }

    .sub-heading {
        font-size: 2vw;
    }
}

@media only screen and (max-width: 480px) {
    .heading {
        margin-bottom: 5px;
        color: var(--black);
        font-size: 6vw;
    }

    .sub-heading {
        font-size: 4vw;
    }
}

#landing-page-container {
    width: 100%;
    top: 0;
    left: 0;
}

.background-svg-1 {
    height: 60vh;
    position: absolute;
    top: 20%;
    right: 7%;
}

.background-svg-2 {
    height: 75vh;
    position: absolute;
    z-index: -1;
    bottom: 0;
    right: 0;
}

#special-container {
    margin-top: 35vh;
    margin-left: 10vw;
}

#special-container h1 {
    font-size: 2.5vw;
}

#special-container p {
    font-size: 1.5vw;
    margin-top: 2vh;
    color: var(--theme);
}

#special-container div {
    margin-top: 6vh;
    font-size: 1vw;
    font-weight: bold;
    z-index: 10;
}


@media only screen and (max-width: 768px) {
    .background-svg-1{
        width: 70vw;
        height: auto;
        top: 50vh;
        left: 50%;
        margin-left: -35vw;
    }

    .background-svg-2 {
        width: 100vw;
        height: auto;
    }

    #landing-page-container {
        text-align: center;
    }

    #special-container {
        margin-top: 23vh;
        margin-left: 0;
    }
    
    #special-container h1 {
        font-size: 3.5vh;
    }

    #special-container p {
        font-size: 2vh;
    }

    #special-container div {
        font-size: 1.7vh;
        font-weight: 400;
    }
}
